    margin-top: -0.6rem;
}

.map-filter-toggle__line:last-child {
    margin-top: 0.6rem;
}

.map-filter {
    position: relative;
}

@media screen and (max-width: 1279px) {
    .map-filter {
        left: 0;
        position: absolute;
        top: 0;
    }
}

.map-heading {
    background-color: #f6f6f6;
    margin: 0;
    padding: 1.6rem 4.5rem 1.4rem 3rem;
    position: relative;
}

.map-heading.-dark {
    background-color: #e6e5e5;
}

.map-heading__title {
    color: #727272;
    font-size: 1.4rem;
    font-weight: 400;
    margin: 0;
    text-transform: uppercase;
}

.map-heading__close {
    transition: all 250ms ease-in-out 0ms;
    position: absolute;
    right: 2rem;
    top: 50%;
    background: none;
    border: 0;
    color: #727272;
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: 700;
    padding: 0;
    transform: translateY(-50%);
}

.map-heading__close:hover, .map-heading__close:focus {
    color: #34d7b1;
}

.map-heading__close::before {
    color: currentColor;
    margin: 0;
    font-weight: 700;
}

.map-types {
    height: calc(100vh - 8rem);
    list-style: none;
    margin: 0;
    padding: 0;
}

.map-types__item {
    flex-basis: auto;
    flex-grow: 1;
    height: calc((100vh - 8rem - 0.01px) / 4);
}

.map-types__link {
    transition: all 250ms ease-in-out 0ms;
    background-color: #212851;
    color: #fff;
    font-family: "Encode Sans Expanded", sans-serif;
    font-size: 1.8rem;
    font-weight: 300;
    height: 100%;
    padding: 1rem;
    text-transform: uppercase;
    width: 100%;
}

.map-types__link:hover, .map-types__link:focus {
    background-color: #161b36;
    outline-offset: -0.2rem;
}

.map-types__link.-is-current {
    background-color: #161b36;
    font-weight: 700;
    outline-offset: -0.2rem;
}

.map-search-results {
    display: none;
}

.map-search-results__list {
    height: calc(100vh - 12.6rem);
    list-style: none;
    margin: 0;
    overflow-x: hidden;
    overflow-y: auto;
    padding: 3rem 0;
    font-family: "Roboto Condensed", sans-serif;
}

@media screen and (max-width: 767px) {
    .map-search-results__list {
        height: calc(100vh - 10.6rem);
    }
}

@media screen and (max-width: 479px) {
    .map-search-results__list {
        height: calc(100vh - 9.2rem);
    }
}

.map-search-results__item:not(:first-child) {
    margin-top: -0.1rem;
}

.map-search-results__item.-no-results {
    font-size: 1.6rem;
    font-weight: 400;
    padding: 0 3rem;
}

.map-search-results__btn {
    transition: all 250ms ease-in-out 0ms;
    background: none;
    border: 0;
    cursor: pointer;
    padding: 1rem 3rem 1rem 6rem;
    position: relative;
    text-align: left;
    width: 100%;
}

.map-search-results__btn::before {
    position: absolute;
    right: 3rem;
    top: 0;
    background-color: #c5c5c5;
    content: '';
    height: 0.1rem;
    width: calc(100% - 6.5rem);
}

.map-search-results__btn::after {
    bottom: 0;
    position: absolute;
    right: 3rem;
    background-color: #c5c5c5;
    content: '';
    height: 0.1rem;
    width: calc(100% - 6.5rem);
}

.map-search-results__btn:focus {
    outline-offset: -0.2rem;
}

.map-search-results__btn:hover, .map-search-results__btn:focus {
    background-color: #c5c5c5;
}

.map-search-results__btn img {
    left: 3.8rem;
    position: absolute;
    top: 1rem;
    display: block;
    height: auto;
    width: 1.5rem;
}

.map-search-results__category {
    transition: all 250ms ease-in-out 0ms;
    color: #ca000a;
    display: block;
    font-family: "Roboto Condensed", sans-serif;
    font-size: 1.2rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
    text-transform: uppercase;
}

.map-search-results__title {
    transition: all 250ms ease-in-out 0ms;
    font-family: "Roboto Condensed", sans-serif;
    color: #000;
    font-size: 1.3rem;
    font-weight: 300;
    margin: 0;
}

.map-toggle-resultats {
    bottom: 1.5rem;
    position: absolute;
    right: 3rem;
    background: none;
    border: 0;
    cursor: pointer;
    display: inline-block;
    font-family: "Encode Sans Expanded", sans-serif;
    font-size: 1.2rem;
    font-weight: 300;
    padding: 0 2rem 0 0;
    z-index: 3;
    display: none;
}

.map-toggle-resultats::after {
    content: "\f06e";
    font-weight: 400;
}

.map-toggle-resultats:hover::before, .map-toggle-resultats:focus::before {
    background-color: #000;
}

.map-toggle-resultats:hover::after, .map-toggle-resultats:focus::after {
    color: #000;
    transform: scale(1.1);
}

.map-toggle-resultats::before {
    transition: all 250ms ease-in-out 0ms;
    position: absolute;
    right: 0.5rem;
    top: -5%;
    height: 110%;
    width: 0;
    background-color: #34d7b1;
    content: '';
    transform: rotate(-30deg);
    z-index: 2;
}

.map-toggle-resultats::after {
    transition: all 250ms ease-in-out 0ms;
    position: absolute;
    right: 0;
    top: 0;
    color: #34d7b1;
    z-index: 1;
}

.map-toggle-resultats.-is-hidden {
    display: none;
}

.map-toggle-resultats.-is-active:hover::before, .map-toggle-resultats.-is-active:focus::before {
    background-color: #000;
}

.map-toggle-resultats.-is-active::before {
    background-color: #34d7b1;
    width: 2px;
}

.marker-message {
    white-space: normal;
    width: 20rem;
}

.marker-message__theme {
    color: #34d7b1;
}

.marker-message__title {
    font-size: 1.4rem;
    margin: 0;
}
